"use client";

import { useEffect, useState } from "react";
import { jwtDecode } from "jwt-decode";

export default function DonorDashboard() {
  const [history, setHistory] = useState([]);
  const [requests, setRequests] = useState([]);
  const [donorId, setDonorId] = useState<string | null>(null);

  // Step 1 — Get donorId from JWT token
  useEffect(() => {
    const token = localStorage.getItem("token");

    if (token) {
      try {
        const decoded: any = jwtDecode(token);
        setDonorId(decoded.userId); // backend should put userId inside token
      } catch (err) {
        console.error("Invalid token:", err);
      }
    }
  }, []);

  // Step 2 — Fetch donation history when donorId is available
  useEffect(() => {
    if (!donorId) return; 

    fetch(`http://localhost:5000/api/donor/history/${donorId}`)
      .then(res => res.json())
      .then(data => setHistory(data))
      .catch(err => console.error(err));
  }, [donorId]);

  // Step 3 — Fetch urgent requests (no login needed)
  useEffect(() => {
    fetch("http://localhost:5000/api/donor/requests")
      .then(res => res.json())
      .then(data => setRequests(data))
      .catch(err => console.error(err));
  }, []);

  return (
    <div className="p-6">
      <h2 className="text-2xl font-bold mb-4">Donation History</h2>

      {!donorId && (
        <p className="text-red-500">Loading donor information...</p>
      )}

      <table>
        <thead>
          <tr>
            <th>Date</th>
            <th>Hospital</th>
            <th>Units</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {history.map((item: any, index) => (
            <tr key={index}>
              <td>{item.date?.slice(0, 10)}</td>
              <td>{item.hospital}</td>
              <td>{item.units}</td>
              <td>{item.status}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <hr className="my-6" />

      <h2 className="text-2xl font-bold mb-4">Urgent Requests</h2>

      {requests.map((req: any, index) => (
        <div key={index} className="border p-3 mb-3 rounded">
          <p><b>{req.bloodType}</b> — {req.unitsNeeded} units</p>
          <p>{req.hospital}</p>
          <p>Urgency: {req.urgency}</p>
          <p>{req.locationKm} km away</p>
        </div>
      ))}
    </div>
  );
}
